.wholePage{
    background-image: linear-gradient(to right, #1e3799, #192a56);
    width: 100%;
    height: 700px;
    display: flex;
    justify-content: center;
    align-items: center;
}
.center{
    display: flex;
    width:70%;
    height:700px;
    margin-top: 350px;
    gap:0, 0;
}

.HeadSide{
    background-image: url('../Assets/logo.jpg');
    background-position: center;
    background-repeat: no-repeat;
    background-size: cover;
    border:1px solid white;
    height: 400px;
    width:50%;
    border-radius: 5px;
}